import { h, Component } from 'preact';
import { Router } from 'preact-router';
import { restoreProgress, resetAllData } from '../lib/flashcards'

import Header from './header';

// Code-splitting is automated for routes
import Sets from '../routes/sets';
import Set from '../routes/set';
import Card from '../routes/card';
import Home from '../routes/home';

import data from '../data.json'

export default class App extends Component {
	
	/** Gets fired when the route changes.
	 *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
	 *	@param {string} event.url	The newly routed URL
	 */

	setShit = whatever => {
		setTimeout(() => {
			this.setState(whatever)
		}, 100)
	}
	handleRoute = e => {
			this.currentUrl = e.url;
			switch(e.url) {
				case "/" : 
					this.setShit({previousUrl: null});
					break;
				case "/sets" :
					this.setShit({previousUrl: "/"});
					break;
				default : {
					const setUrl = e.url.match(/\/sets\/\d/)[0]
					if (e.url.match(/\/sets\/\d+$/)) {
						this.setShit({previousUrl: "/sets"});
					} else if (e.url.match(/\/sets\/\d+\/cards\/\d+/)) {
						this.setShit({previousUrl: setUrl});
					} else {
						this.setShit({previousUrl: null});
					}
				}
			}
	};

	restoreData = () => {
		this.setState({data: restoreProgress(data)})		
	}

	handleResetAllDataClick = () => {
		if (confirm("Do you want to reset all data?")) {
			resetAllData()
			this.setState({data: null})
			this.restoreData()
		}
	}

	componentWillMount() {
		this.restoreData()
	}

	render(props, state) {
		return (
			<div id="app">
				<Header backButtonLocation={ state.previousUrl } />
				<Router onChange={ this.handleRoute }>
          <Home default path="/" handleResetAllDataClick={ this.handleResetAllDataClick } />
					<Sets path="/sets" data={ state.data } />
					<Set path="/sets/:set" data={ state.data } />
					<Card path="/sets/:set/cards/:card" data={ state.data } />
				</Router>
			</div>
		);
	}
}



